import {usersAPI} from '../api/api';
import {updateObjectInArray} from '../utils/object-helpers';


const FOLLOW = 'usersPage/FOLLOW';
const UNFOLLOW = 'usersPage/UNFOLLOW';
const SET_USERS = 'usersPage/SET_USERS';
const SET_CURRENT_PAGE = 'usersPage/SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'usersPage/SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'usersPage/TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'usersPage/TOGGLE_IS_FOLLOWING_PROGRESS';


let initialState = {
    users: [
        // {id: 1, photoUrl: 'https://o-krohe.ru/images/article/orig/2019/02/mister-bin-uhodit-v-dekretnyj-otpusk.jpg', followed: false, fullName: 'Dmitry', status: 'I am a boss', location: {city: 'Minsk', country: 'Belarus'}},
        // {id: 2, photoUrl: 'https://o-krohe.ru/images/article/orig/2019/02/mister-bin-uhodit-v-dekretnyj-otpusk.jpg', followed: true, fullName: 'Sasha', status: 'I am a boss too', location: {city: 'Moscow', country: 'Russia'}},
        // {id: 3, photoUrl: 'https://o-krohe.ru/images/article/orig/2019/02/mister-bin-uhodit-v-dekretnyj-otpusk.jpg', followed: false, fullName: 'Andrew', status: 'I am a boss too', location: {city: 'Kiev', country: 'Ukraine'}}
    ],
    pageSize: 20,  //     
    totalUsersCount: 0,  //   
    currentPage: 1,  //  
    isFetching: false,  //     Preloader
    followingInProgress: [],     //   id ,      
    // fake: 10
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        // case "FAKE": return {...state, fake: state.fake + 1}
        // case FOLLOW: {
        //     return {
        //         ...state,
        //         // users: [...state.users],
        //         users: state.users.map(u => {
        //             if (u.id === action.userId) {
        //                 return {...u, followed: true}
        //             }
        //             return u;
        //         })
        //     };
        // }
        // case UNFOLLOW: {
        //     return {
        //         ...state,
        //         users: state.users.map(u => {
        //             if (u.id === action.userId) {
        //                 return {...u, followed: false}
        //             }
        //             return u;
        //         })
        //     };
        // }
        case FOLLOW: {
            return {
                ...state,
                // users: [...state.users],
                users: updateObjectInArray(state.users, "id", action.userId, {followed: true})
            };
        }
        case UNFOLLOW: {
            return {
                ...state,
                users: updateObjectInArray(state.users, "id", action.userId, {followed: false})
            };
        }
        case SET_USERS: {
            return {
                ...state,
                users: action.users
            };
        }
        case SET_CURRENT_PAGE: {
            return {
                ...state,
                currentPage: action.currentPage
            };
        }
        case SET_TOTAL_USERS_COUNT: {
            return {
                ...state,
                totalUsersCount: action.count
            };
        }
        case TOGGLE_IS_FETCHING: {
            return {
                ...state,
                isFetching: action.isFetching
            };
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS: {
            return {
                ...state,
                followingInProgress: action.isFetching
                ? [...state.followingInProgress, action.userId]     //  isFetching = true
                : state.followingInProgress.filter(id => id != action.userId)       //  isFetching = false
            };
        }
        default: {
            return state;
        }
    }
}

export const followSuccess = (userId) => ({type: FOLLOW, userId});

export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId});

export const setUsers = (users) => ({type: SET_USERS, users});

export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage});

export const setTotalUsersCount = (totalUsersCount) => ({type: SET_TOTAL_USERS_COUNT, count:totalUsersCount});

export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});

export const toggleFollowingProgress = (isFetching, userId) => ({type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId});

export const requestUsers = (page, pageSize) => { 
    return async (dispatch) => {
        dispatch(toggleIsFetching(true));
        let data = await usersAPI.getUsers(page, pageSize)
        dispatch(setCurrentPage(page));
        dispatch(toggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
    }
};

